@charset "utf-8";

@import "font.scss";
@import "reset.scss";
@import "common.scss";

body {background: #f7f7f7;}

#wrap {padding: 100px 0 0;}

#header {position: fixed; top: 0; right: 0; left: 0; height: 100px; background: #000000;
  .logo {position: absolute; top: 25px; left: 40px;
    a {display: block; font-family: Roboto; font-weight: 700; font-size: 40px; line-height: 50px; color: #ffffff;}
  }
}

#home {padding: 36px;
  ul {
    li {margin: 36px 0 0;
      &:first-child {margin-top: 0;}
      a {display: block; width: 100%; padding: 50px 0; background: #ffffff; text-align: center; box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3); font-size: 30px;}
    }
  }
}

#asset {padding: 100px 36px;
  h2 {font-weight: 700; font-size: 32px; text-align: center;}
  .bothbox {overflow: hidden; width: 500px; margin: 60px auto 0;
    input {float: left; width: 340px;}
    button {float: right; width: 150px; height: 60px; background: #000000; color: #ffffff; border-radius: 8px;}
  }
  .result {margin: 50px 0 0; text-align: center;
    strong {color: red;}
  }
  .notice {margin: 20px 0 0; font-size: 22px; color: #666666; text-align: center;}
}